///////// 1200px /////////

@media only screen and (max-width: 1200px) {
  .section-news .news-link {
    max-height: 108px;
  }
}

///////// 1087px /////////

@media only screen and (max-width: 1087px) {
  .section-news .news-link {
    max-height: 126px;
  }
  .sidebar-link {
    max-height: 111px;
  }
  .catalog .catalog-news-box.modify {
    max-width: initial;
  }
}

///////// 928px /////////

@media only screen and (max-width: 928px) {
  .section-news .news-link {
    max-height: 126px;
  }
  .sidebar-link {
    max-height: 126px;
  }
}

///////// 920px /////////

@media only screen and (max-width: 920px) {
  .container {
    padding: 0 20px;
  }
  //NAV
  .nav {
    display: none;
  }
  .section-news .container {
    gap: 0;
  }
  .header-bottombar .container {
    position: relative;
  }
  .mobile-nav {
    position: absolute;
    top: 42px;
    left: 0;
    right: 0;
    padding: 0 20px;
    max-width: 1200px;
    background: $nav-mobile-background;
    z-index: 500;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
    &.show {
      display: block;
    }
    &-list {
      display: flex;
      flex-direction: column;
    }
    &-item {
      transition: background-color 0.25s ease;
      &:hover {
        background-color: #289dcc;
      }
      &:hover .mobile-nav-link {
        color: $secondary-text-color;
      }
      &:not(:first-child) {
        border-top: 1px solid rgba(255, 255, 255, 0.1);
      }
    }
    &-link {
      padding: 14px 20px;
      display: flex;
      line-height: 22px;
      font-size: 14px;
      font-weight: 400;
      text-transform: uppercase;
      color: $secondary-text-color;
      user-select: none;
      transition: color 0.25s ease;
    }
  }
  .mobile-home-link {
    display: flex;
    padding: 0;
    height: 42px;
    width: 44px;
    align-items: center;
    justify-content: center;
    background-color: #289dcc;
    &.hide {
      background-color: transparent;
    }
    &:hover {
      background-color: #289dcc;
    }
  }
  .nav-home-icon {
    display: inline-flex;
    height: 20px;
    width: 26px;
  }

  //BURGER
  .header-burger {
    position: relative;
    height: 42px;
    width: 44px;
    cursor: pointer;
    &:hover {
      background-color: #289dcc;
    }
    &-box {
      display: block;
      position: absolute;
      width: 22px;
      height: 20px;
      z-index: 4;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      &::before,
      &::after {
        content: "";
        background-color: #21243d;
        position: absolute;
        width: 100%;
        height: 4px;
        left: 0;
        transition: all 0.3s ease 0s;
        color: #fff;
        background: #fff;
        border-radius: 1px;
      }
      &::before {
        top: 1px;
      }
      &::after {
        bottom: 1px;
      }
    }
    &-line {
      background-color: #21243d;
      position: absolute;
      width: 100%;
      height: 4px;
      left: 0;
      top: 8px;
      background: #fff;
      border-radius: 1px;
    }
  }

  //SECTION-HERO
  .section-hero-content {
    flex-direction: column;
  }
  .swiper {
    max-width: 100%;
    max-height: 430px;
    min-height: 260px;
  }
  .swiper-wrapper {
    max-height: 430px !important;
    min-height: 260px;
  }
  .section-hero-link {
    max-width: 100%;
    &.news1 {
      max-width: 100%;
    }
  }
  .section-hero-poster {
    max-width: 100%;
    max-height: 210px;
    &.modify {
      max-width: 100%;
      max-height: 430px;
    }
  }
  .section-hero-title {
    &.modify {
      font-size: clamp(15px, 3.8vw, 22px);
      line-height: clamp(18px, 4.6vw, 28px);
    }
  }
  .section-hero-description.modify {
    left: 20px;
  }

  //SECTION-NEWS
  .section-news .container {
    flex-direction: column;
  }
  .section-news .news-link {
    max-width: 100%;
    max-height: 108px;
    &.modify {
      max-width: 100%;
    }
  }
  .section-news .news-poster.modify {
    max-width: 100%;
  }
  .section-news-subcontent {
    margin-bottom: 50px;
  }
  .sidebar-link {
    max-width: 100%;
  }
  .sidebar {
    max-width: 100%;
    padding-bottom: 80px;
  }
  .sidebar-link-poster.modify {
    max-height: 100%;
  }
  .sidebar-link-content {
    width: 100%;
  }
  .sidebar-link-title {
    font-size: 14px;
  }
  .section-news .news-box {
    max-width: initial;
  }
  //SECTION-NEWS
  .catalog .container {
    flex-direction: column;
  }
  .catalog .catalog-news {
    max-width: initial;
  }
  .catalog .catalog-news-poster {
    max-width: initial;
  }
  .catalog .container .sidebar {
    padding-bottom: 0;
  }
  .section-touch-content {
    gap: 80px;
  }
  .page-news .container {
    flex-direction: column;
  }
  .page-news .container .sidebar {
    padding-bottom: 0;
  }
}

///////// 768px /////////

@media only screen and (max-width: 768px) {
  .section-news .container {
    gap: initial;
  }
  .section-news .news-links {
    grid-template-columns: 1fr 1fr;
  }
  .section-news .news-link {
    max-height: 110px;
  }
  .section-news .news-box-title {
    line-height: 16px;
    font-size: 12px;
  }
  .section-news .news-page.mod {
    margin-bottom: 6px;
  }
  .catalog .news-subbox-title {
    font-size: 20px;
    line-height: 25px;
  }
  .page-news .news-img-caption {
    font-size: 13px;
    line-height: 17px;
  }
  .page-news .news-subbox-title {
    margin-bottom: 8px;
  }
  .page-news .news-subbox-subtitle {
    margin-bottom: 14px;
  }
  .footer-content .container {
    flex-wrap: wrap;
  }
  .footer-box:nth-of-type(1) {
    flex: 1 1 100%;
  }
  .footer-box-text {
    margin-bottom: 30px;
  }
  .footer-copyright .container {
    padding-top: 60px;
  }
  .section-touch-content {
    flex-direction: column;
  }
  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }
  .scroll-up {
    display: none;
  }
  .btn-primary.modify {
    font-size: 16px;
    padding: 3px 28px;
  }
}

///////// 600px /////////

@media only screen and (max-width: 600px) {
  .section-news-subcontent {
    flex-direction: column;
    margin-bottom: 0;
  }
  .section-news .news-links {
    display: flex;
    grid-template-areas: none;
    grid-template-columns: none;
    flex-wrap: wrap;
  }
  .section-news .news-link.news1 {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .section-news .news-link {
    width: 100%;
    max-height: 90px;
  }
  .section-news .news-poster.modify {
    max-height: 100%;
  }
  .section-news .news-box {
    max-width: initial;
    width: 100%;
  }
  .section-news-life {
    margin-right: 0;
    margin-bottom: 50px;
  }
  .section-news-politics {
    margin-bottom: 50px;
  }
  .section-news .news-box-title {
    line-height: 19px;
    font-size: 15px;
  }
  .section-news .sidebar-link-title {
    &.modify {
      font-size: clamp(19px, 1.8vw, 21px);
      line-height: clamp(23px, 2.4vw, 26px);
    }
  }
  .section-news .news-poster {
    height: 90px;
    &.modify {
      height: auto;
    }
  }
  .sidebar-link-poster {
    height: 90px;
    &.modify {
      height: auto;
    }
  }
  .catalog .catalog-news-boxes {
    grid-template-columns: initial;
  }
  .catalog .catalog-news-poster.modify {
    max-height: initial;
  }
  .catalog .news-subbox-title {
    font-size: 22px;
    line-height: 27px;
    &.modify {
      font-size: clamp(26px, 4vw, 30px);
      line-height: clamp(29px, 4.8vw, 36px);
    }
  }
  .footer-copyright .container {
    flex-direction: column-reverse;
    text-align: center;
  }
  .footer .social-list {
    margin-bottom: 8px;
    margin-right: 0;
    justify-content: center;
  }
  .catalog-politics .politics-title {
    font-size: 30px;
  }
  .catalog-politics .politics-img {
    width: 300px;
  }
}

///////// 455px /////////

@media only screen and (max-width: 455px) {
  .section-news .news-box-title {
    font-size: 14px;
  }
}

@media only screen and (max-width: 438px) {
  .section-news .news-link {
    max-height: 108px;
  }
  .section-news .news-poster {
    height: 108px;
  }
  .section-hero .wrapper-grid {
    grid-template-areas:
      "news2"
      "news3"
      "news4"
      "news5";
  }
  .section-hero-description {
    left: 14px;
    &.modify {
      left: 14px;
    }
  }
}

///////// 400px /////////

@media only screen and (max-width: 400px) {
  .header-topbar-date {
    font-size: 13px;
  }
  .header-topbar .social-link {
    font-size: 20px;
  }
  .header-topbar .social-list {
    gap: 16px;
  }
  .live {
    display: none;
  }
  .btn-primary.modify {
    margin-left: auto;
  }
}

///////// 380px /////////

@media only screen and (max-width: 380px) {
  .section-hero-content {
    display: flex;
    flex-direction: column;
    grid-template-areas: none;
  }
  .section-hero-poster.modify {
    max-height: 280px;
  }

  .header-topbar .social-list {
    gap: 14px;
  }
  .section-news .news-box-title {
    line-height: 17px;
    font-size: 13px;
  }
  .section-news .sidebar-link-title {
    line-height: 17px;
    font-size: 13px;
  }
  .section-news .news-poster {
    height: 100px;
  }
  .section-news .news-link {
    max-height: 100px;
  }
}

@media only screen and (max-width: 369px) {
  .section-news .news-link {
    max-height: 119px;
  }
  .section-news .news-poster {
    height: 119px;
  }
  .section-touch .touch-link-label {
    font-size: 16px;
    line-height: 1.6em;
  }
  .section-hero .news-themes {
    margin-bottom: 10px;
  }
  .section-hero .news-themes-tag {
    font-size: 11px;
    line-height: 15px;
  }
}

@media only screen and (max-width: 329px) {
  .section-news .news-box-title {
    line-height: 16px;
    font-size: 12px;
  }
  .section-news .sidebar-link-title {
    line-height: 16px;
    font-size: 12px;
  }
}

///////// 320px /////////

@media only screen and (max-width: 320px) {
  .header-topbar-date {
    font-size: 12px;
  }
  .header-topbar .social-list {
    gap: 12px;
  }
  .header-topbar .social-link {
    font-size: 18px;
  }
  .sidebar-link-poster {
    height: 120px;
  }
  .catalog .news-subbox-title.modify {
    font-size: 28px;
    line-height: 34px;
  }
}
