@import "mixins";
@import "variables";

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
  font: inherit;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  font-family: $primary-font;
  line-height: 1.5;
  background-color: $body-background;
  color: $primary-text-color;
  &.lock {
    overflow: hidden;
  }
}

body::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #878686;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: #f9f9fd;
}

.container {
  @include container;
}

///////// GENERAL /////////

.logo {
  display: inline-flex;
  flex-direction: column;
  &-name {
    font-size: clamp(35px, 3.4vw, 38px);
    line-height: clamp(35px, 3.7vw, 42px);
    font-weight: bold;
    letter-spacing: -1px;
    &-first {
      display: inline-flex;
      color: #555;
    }
    &-second {
      display: inline-flex;
      color: #289dcc;
    }
  }
  &-description {
    font-size: clamp(12px, 1.15vw, 13px);
    line-height: 16px;
    letter-spacing: -0.5px;
    color: $primary-text-color;
  }
}
.btn-primary {
  display: inline-flex;
  padding: 10px 30px;
  outline: 0;
  border: none;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  color: #e5e5f2;
  letter-spacing: 0.3px;
  line-height: 24px;
  text-decoration: none;
  background: #2286ad;
  font-family: inherit;
  z-index: 600;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
  &:active {
    transform: scale(0.95);
  }
  &.modify {
    padding: 4px 28px;
    margin-left: auto;
    border-radius: 3px;
    background: #a31f1f;
    align-items: center;
    font-size: 17px;
    align-items: center;
    &:active {
      transform: initial;
    }
  }
}
.btn-secondary {
  display: inline-flex;
  font-size: 12px;
  padding: 5px 10px;
  color: $secondary-text-color;
  background-color: #289dcc;
  border-radius: 3px;
  border: none;
  outline: none;
  transition: background-color 0.25s ease;
  &:hover {
    background-color: #2286ad;
  }
}
.mobile-nav {
  display: none;
}
.mobile-home-link {
  display: none;
}
.swiper {
  max-width: 630px;
  max-height: 350px;
  flex: 1 1 58%;
}
.swiper-wrapper {
  max-height: 350px !important;
}
.swiper-slide {
  display: flex;
  height: auto;
}
.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  bottom: 20px;
  background: rgb(40 157 204 / 80%);
  border-radius: 3px;
  z-index: 50;
  width: 28px;
  height: 30px;
  color: white;
  margin-top: initial;
  top: initial;
  left: initial;
  transition: all 0.15s ease;
}
.swiper-button-prev {
  right: 52px;
  &:hover {
    background: rgb(40, 158, 204);
  }
}
.swiper-button-next {
  right: 20px;
  &:hover {
    background: rgb(40, 158, 204);
  }
}
.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 13px;
  color: white;
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(204, 44, 44, 0.4);
    box-shadow: 0 0 0 0 rgba(204, 44, 44, 0.4);
  }
  70% {
    -moz-box-shadow: 0 0 0 13px rgba(204, 169, 44, 0);
    box-shadow: 0 0 0 13px rgba(204, 169, 44, 0);
  }
  100% {
    -moz-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
  }
}

///////// SCROLL-UP /////////

.scroll-up {
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
  background: $scroll-background;
  text-align: center;
  height: 48px;
  width: 48px;
  border-radius: 0px;
  position: fixed;
  right: 40px;
  bottom: 40px;
  z-index: 999;
  &-icon {
    width: 30px;
    height: 30px;
    fill: $scroll-color;
  }
  &:hover {
    background: rgb(40, 158, 204);
  }
}
.show-scroll {
  opacity: 1;
  visibility: visible;
}

///////// HEADER /////////

.header {
  &-topbar {
    .container {
      padding-top: 3px;
      padding-bottom: 3px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: $section-background;
      border-bottom: 1px solid rgba(128, 128, 128, 0.1);
      // box-shadow: 0 0 2px 0 rgb(0 0 0 / 10%);
    }
    &-date {
      font-size: 14px;
      font-weight: 400;
      line-height: 16px;
    }
    .social-list {
      display: flex;
      gap: 20px;
    }
    .social-item {
      display: inline-flex;
    }
    .social-link {
      font-size: 22px;
      & i {
        transition: color 0.1s ease;
      }
      .facebook {
        color: #3b5a9b;
      }
      .twitter {
        color: #1bb2e9;
      }
      .instagram {
        color: #3f729b;
      }
      .telegram {
        color: #25a3e1;
      }
      &:hover i {
        color: #db4545;
      }
    }
  }
  &-logobar {
    .container {
      padding-top: 36px;
      padding-bottom: 36px;
      background-color: $section-background;
    }
  }
  &-bottombar {
    .container {
      display: flex;
      align-items: center;
      background-color: $header-bottombar-background;
      border-top: 4px solid #289dcc;
      min-height: 46px;
    }
    .nav-list {
      display: flex;
      align-items: center;
    }
    .nav-item {
      display: inline-flex;
      white-space: nowrap;
    }
    .nav-link {
      padding: 13px 12px;
      color: $secondary-text-color;
      font-size: 14px;
      font-weight: 500;
      line-height: 16px;
      text-transform: uppercase;
      transition: background-color 0.25s ease;
      &.modify {
        padding: 0;
        height: 42px;
        width: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #289dcc;
      }
      &.hide {
        background: none;
      }
      &:hover {
        background-color: #289dcc;
      }
      &.show {
        background-color: #289dcc;
      }
    }
    .nav-home-icon {
      display: inline-flex;
      height: 20px;
      width: 26px;
      transition: background-color 0.25s ease;
    }
    .header-search-icon {
      font-size: 17px;
      display: inline-flex;
      margin-left: auto;
      color: #fff;
      align-items: center;
      cursor: pointer;
    }
  }
}

///////// SECTION-HERO /////////

.section-hero {
  .container {
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: $section-background;
  }
  &-content {
    display: flex;
    gap: 10px;
  }
  .wrapper-grid {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 10px;
    grid-template-areas:
      "news2 news3"
      "news4 news5";
  }
  &-link {
    max-width: 245px;
    position: relative;
    display: inline-grid;
    width: 100%;
    &::after {
      content: "";
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.1) 0%,
        rgba(0, 0, 0, 0.25) 40%,
        rgba(0, 0, 0, 0.5) 80%
      );
      width: 100%;
      height: 100%;
      display: flex;
      position: absolute;
      z-index: 1;
    }
    &.news1 {
      max-width: 630px;
      grid-area: news1;
    }
    &.news2 {
      grid-area: news2;
    }
    &.news3 {
      grid-area: news3;
    }
    &.news4 {
      grid-area: news4;
    }
    &.news5 {
      grid-area: news5;
    }
  }
  &-poster {
    display: inline-flex;
    max-width: 245px;
    max-height: 170px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    &.modify {
      max-width: 630px;
      max-height: 350px;
    }
  }
  &-description {
    position: absolute;
    padding-right: 10px;
    bottom: 16px;
    left: 14px;
    z-index: 2;
    &.modify {
      padding-right: 30px;
      bottom: 26px;
      left: 30px;
    }
  }
  .news-themes {
    display: flex;
    gap: 6px;
    margin-bottom: 12px;
  }
  .news-themes-tag {
    display: inline-flex;
    border-radius: 3px;
    color: #ffffff;
    font-size: 12px;
    line-height: 17px;
    padding: 3px 10px;
    &:first-child {
      background: #03799e;
    }
    &:last-child {
      background: #22a48e;
    }
  }
  &-title {
    margin-bottom: 8px;
    color: $secondary-text-color;
    font-size: clamp(14px, 2.7vw, 15px);
    line-height: clamp(17px, 3.1vw, 19px);
    font-weight: 500;
    letter-spacing: 0.12px;
    max-width: 200px;
    text-shadow: 0 0 10px #000;
    transition: color 0.25s ease;
    &:hover {
      color: #289dcc;
    }
    &.modify {
      margin-bottom: 10px;
      color: rgba(255, 255, 255, 0.9);
      font-size: clamp(21px, 2.2vw, 22px);
      font-weight: 600;
      line-height: 28px;
      max-width: 500px;
      &:hover {
        color: #289dcc;
      }
    }
  }
  &-info {
    display: flex;
    align-items: center;
    color: $secondary-text-color;
    font-size: 11px;
    font-weight: 300;
    line-height: 12px;
    letter-spacing: 0.1px;
  }
  .calendar-icon,
  .person-icon {
    margin-right: 4px;
  }
  &-date {
    &.modify {
      margin-right: 13px;
    }
  }
}

///////// SECTION-NEWS /////////

.section-news {
  .container {
    display: flex;
    gap: 30px;
    background-color: $section-background;
    .sidebar {
      flex: 1 1 30%;
    }
  }
  &-content {
    flex: 1 1 76%;
  }
  &-war {
    margin-bottom: 50px;
    position: relative;
  }
  &-subcontent {
    display: flex;
    margin-bottom: 80px;
    & .news-link.modify.news1 {
      margin-right: 0;
    }
  }
  &-life {
    position: relative;
    margin-right: 20px;
    & .news-link.modify.news1 {
      margin-bottom: 20px;
    }
    & .news-link:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  &-politics {
    position: relative;
    & .news-link.modify.news1 {
      margin-bottom: 20px;
    }
    & .news-link:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  .news-links {
    display: flex;
    &.modify {
      display: flex;
      flex-direction: column;
      grid-template-areas: none;
    }
  }
  .news-link {
    display: flex;
    max-width: 390px;
    max-height: 90px;
    &.modify {
      max-width: 390px;
      max-height: 100%;
      display: flex;
      flex-direction: column;
    }
    &.news1 {
      margin-right: 20px;
      flex: 1 1 62%;
    }
    &-wrapper {
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
  }
  .news-title {
    margin-bottom: 14px;
    padding: 8px 11px;
    display: inline-flex;
    background-color: $war-title-background;
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.3px;
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      background-color: $war-title-background;
      display: flex;
      position: absolute;
      top: 32px;
      left: 0;
    }
    &.modify_life {
      background-color: $life-title-background;
      &::after {
        background-color: $life-title-background;
      }
    }
    &.modify_politics {
      background-color: $politics-title-background;
      &::after {
        background-color: $politics-title-background;
      }
    }
  }
  .news-page {
    display: flex;
    &.modify {
      margin-bottom: 12px;
    }
    &.mod {
      margin-bottom: 9px;
    }
  }
  .news-poster {
    display: inline-flex;
    width: 130px;
    height: auto;
    object-fit: cover;
    object-position: center;
    &.modify {
      max-width: 390px;
      max-height: 215px;
      width: 100%;
    }
  }
  .news-box {
    padding: 16px 20px 16px 14px;
    max-width: 260px;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
    &.modify {
      padding: 14px 14px 20px;
      max-width: initial;
    }
  }
  .news-box-title {
    color: #444;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.1px;
    transition: color 0.25s ease;
    &.modify {
      margin-bottom: 0;
      font-size: clamp(19px, 1.8vw, 21px);
      font-weight: 400;
      line-height: clamp(23px, 2.4vw, 26px);
    }
    &:hover {
      color: #289dcc;
    }
  }
  .news-box-info {
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    color: #444444cc;
    font-size: 11px;
    font-weight: 300;
    line-height: 12px;
    letter-spacing: 0.1px;
    &.modify {
      margin-bottom: 0;
    }
  }
  .calendar-icon {
    margin-right: 4px;
  }
  .news-box-date {
    margin-right: 13px;
  }
  .person-icon {
    margin-right: 4px;
  }
  .news-box-text {
    font-size: clamp(13px, 1.2vw, 14px);
    font-weight: 400;
    line-height: 21px;
    color: $primary-text-color;
  }
}

///////// PAGE-NEWS /////////

.page-news {
  .container {
    display: flex;
    gap: 30px;
    padding-top: 30px;
    padding-bottom: 80px;
    background-color: $section-background;
    .sidebar {
      flex: 1 1 30%;
    }
  }
  .poster-wrapper {
    display: inline-flex;
  }
  .news-box {
    display: flex;
    flex-direction: column;
    flex: 1 1 76%;
  }
  .news-box-img {
    margin-bottom: 15px;
    max-height: 445px;
    display: inline-flex;
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: center;
    &.modify {
      margin-bottom: 0;
    }
  }
  .news-img-caption {
    display: flex;
    margin-top: 5px;
    margin-bottom: 22px;
    display: flex;
    color: $primary-text-color;
    opacity: 0.75;
    font-size: 14px;
  }
  .news-subbox {
    padding: 0 15px 30px 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
  }
  .news-subbox-title {
    margin-bottom: 6px;
    color: $primary-text-color;
    font-size: clamp(26px, 4vw, 32px);
    line-height: clamp(29px, 4.8vw, 40px);
    font-weight: 500;
    letter-spacing: 0.1px;
  }
  .bookmark {
    margin-right: 8px;
    color: #289dcc;
  }
  .news-subbox-subtitle {
    margin-bottom: 12px;
    color: $primary-text-color;
    font-size: clamp(17px, 2.5vw, 20px);
    line-height: clamp(23px, 3.5vw, 28px);
    font-weight: 500;
    letter-spacing: 0.1px;
  }
  .news-box-info {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    color: rgba(68, 68, 68, 0.8);
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    letter-spacing: 0.1px;
  }
  .calendar-icon {
    margin-right: 4px;
  }
  .news-box-date {
    margin-right: 13px;
  }
  .person-icon {
    margin-right: 4px;
  }
  .news-subbox-text {
    font-size: clamp(16px, 2.3vw, 18px);
    line-height: clamp(23px, 3.4vw, 28px);
    font-weight: 400;
    color: #444;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  .news-subbox-link {
    color: #0914ff;
  }
}

///////// CATALOG-NEWS /////////

.catalog {
  .container {
    padding-top: 30px;
    padding-bottom: 80px;
    display: flex;
    gap: 30px;
    background-color: $section-background;
  }
  .catalog-news {
    max-width: 800px;
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1 1 89%;
  }
  .catalog-news-title {
    margin-bottom: 14px;
    padding: 8px 11px;
    display: inline-flex;
    align-self: flex-start;
    background-color: $war-title-background;
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.3px;
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      background-color: $war-title-background;
      display: flex;
      position: absolute;
      top: 32px;
      left: 0;
    }
    &.modify_life {
      background-color: $life-title-background;
      &::after {
        background-color: $life-title-background;
      }
    }
    &.modify_politics {
      background-color: $politics-title-background;
      &::after {
        background-color: $politics-title-background;
      }
    }
  }
  .catalog-news-boxes {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(1, 1fr);
    grid-gap: 20px;
  }
  .catalog-news-box {
    // margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
    &:nth-last-child(2) {
      margin-bottom: 0;
    }
    &.mf {
      margin-bottom: 30px;
    }
    &.modify {
      max-width: 390px;
    }
  }
  .catalog-news-link {
    display: flex;
  }
  .catalog-news-poster {
    display: inline-flex;
    max-width: 800px;
    max-height: 445px;
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: center;
    &.modify {
      max-height: 216px;
    }
  }
  .catalog-news-subbox {
    padding: 15px 15px 25px 15px;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
  }
  .news-subbox-title {
    margin-bottom: 10px;
    color: $primary-text-color;
    font-size: 24px;
    font-weight: 500;
    line-height: 31px;
    letter-spacing: 0.1px;
    transition: color 0.25s ease;
    &.modify {
      font-size: 32px;
      line-height: 38px;
    }
    &:hover {
      color: #289dcc;
    }
  }
  .news-subbox-info {
    display: flex;
    align-items: center;
    white-space: nowrap;
    margin-bottom: 30px;
    color: rgba(68, 68, 68, 0.8);
    font-size: 12px;
    font-weight: 400;
    line-height: 8px;
    letter-spacing: 0.1px;
  }
  .calendar-icon {
    margin-right: 4px;
  }
  .news-subbox-date {
    margin-right: 13px;
  }
  .person-icon {
    margin-right: 4px;
  }
  .news-subbox-text {
    margin-bottom: 15px;
    font-size: 15px;
    color: $primary-text-color;
  }
}

///////// SIDEBAR /////////

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-left: auto;
  position: relative;
  max-width: 310px;
  flex: 1 1 35%;
  .container {
    max-width: 310px;
    padding: 0;
  }
  &-title {
    margin-bottom: -1px;
    padding: 8px 11px;
    display: inline-flex;
    background-color: $aside-title-background;
    color: $secondary-text-color;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    display: flex;
    align-self: flex-start;
    letter-spacing: -0.3px;
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      background-color: $aside-title-background;
      display: flex;
      position: absolute;
      top: 32px;
      left: 0;
    }
    &.modify_about {
      margin-bottom: 34px;
      background-color: $about-title-background;
      &::after {
        background-color: $about-title-background;
      }
    }
  }
  &-link {
    display: flex;
    max-width: 310px;
    max-height: 90px;
    &.modify {
      max-height: initial;
      margin-bottom: 5px;
      flex-direction: column;
    }
  }
  &-link-poster {
    display: inline-flex;
    width: 130px;
    min-height: 90px;
    object-fit: cover;
    object-position: center;
    &.modify {
      max-height: 160px;
      width: 100%;
      height: 100%;
    }
  }
  &-link-content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 9px 12px 9px 14px;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
    &.modify {
      align-items: initial;
      display: initial;
      padding: 14px 14px 20px 14px;
    }
  }
  &-link-title {
    color: $primary-text-color;
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.1px;
    transition: color 0.25s ease;
    &.modify {
      font-size: clamp(19px, 1.8vw, 21px);
      line-height: clamp(23px, 2.4vw, 26px);
    }
    &:hover {
      color: #289dcc;
    }
  }
  .news-page {
    display: flex;
  }
  .news-page.sidebar1 {
    margin-bottom: 12px;
  }
  .news-page.sidebar2 {
    margin-bottom: 7px;
  }
  &-link-info {
    margin-bottom: 0;
    display: flex;
    align-items: center;
    color: #444444cc;
    font-size: 11px;
    font-weight: 300;
    line-height: 12px;
    letter-spacing: 0.1px;
    &.modify {
      margin-bottom: 16px;
    }
    .calendar-icon {
      margin-right: 4px;
    }
    .person-icon {
      margin-right: 4px;
    }
    .sidebar-link-date {
      margin-right: 13px;
    }
  }
  &-link-text {
    font-size: clamp(13px, 1.2vw, 14px);
    font-weight: 400;
    line-height: 21px;
    color: $primary-text-color;
  }
  &-about {
    display: flex;
    margin-top: 25px;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    .logo {
      margin-bottom: 36px;
    }
    &-text {
      font-size: clamp(13px, 1.2vw, 14px);
      font-weight: 400;
      line-height: 21px;
      color: $primary-text-color;
    }
  }
}

///////// SECTION-TOUCH /////////

.section-touch {
  .container {
    padding-top: 30px;
    padding-bottom: 80px;
    background-color: $section-background;
  }
  &-title {
    margin-bottom: 30px;
    color: $primary-text-color;
    font-size: 32px;
    font-weight: 500;
    line-height: 40px;
    letter-spacing: 0.1px;
  }
  &-content {
    display: flex;
    gap: 100px;
  }
  &-form {
    max-width: 600px;
    width: 100%;
  }
  .touch-form-row {
    display: flex;
    margin-bottom: 24px;
    gap: 24px;
    border-radius: 4px;
    textarea {
      padding: 12px 12px;
    }
  }
  .touch-form-field {
    padding: 8px 12px;
    width: 100%;
    border: none;
    border: 1px solid #a4a4a5;
    background-color: rgba(0, 0, 0, 0);
    resize: none;
    outline: 0;
    font-size: 15px;
    line-height: 12px;
    color: #494d50;
    font-family: inherit;
    border-radius: 4px;
    &:focus {
      outline: 0;
      border: 1px solid #44afd9;
      &::placeholder {
        color: transparent;
      }
    }
  }
  &-box {
    position: relative;
  }
  .section-subtitle {
    margin-bottom: 21px;
    padding: 8px 11px;
    display: inline-flex;
    background-color: #2286ad;
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.3px;
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      background-color: #2286ad;
      display: flex;
      position: absolute;
      top: 32px;
      left: 0;
    }
  }
  &-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  &-link {
    display: flex;
    align-items: center;
  }
  .label-icon {
    font-size: 22px;
    margin-right: 6px;
    color: #2286ad;
  }
  .touch-link-label {
    font-size: 17px;
    line-height: 1.7em;
    color: $primary-text-color;
  }
}

///////// PAGE-ABOUT /////////

.page-about {
  .container {
    padding-top: 30px;
    padding-bottom: 80px;
    background-color: #fafafa;
  }
  &-box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    border-radius: 5px;
    width: 100%;
    height: 250px;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url(../images/about/1.webp);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  &-title {
    text-transform: uppercase;
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    line-height: 40px;
    letter-spacing: 0.1px;
  }
  &-content {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &-item {
    display: flex;
    align-items: center;
  }
  .check-icon {
    margin-right: 12px;
  }
  &-item .social-link {
    font-size: 20px;
    & .facebook {
      color: #3b5a9b;
    }
    & .twitter {
      color: #1bb2e9;
    }
    & .instagram {
      color: #3f729b;
    }
    & .telegram {
      color: #25a3e1;
    }
  }
  &-item .social-link i {
    transition: color 0.1s ease;
  }
  &-item .social-link:hover i {
    color: #db4545;
  }
  &-item .social-link:not(:last-child) {
    margin-right: 15px;
  }
  &-item .social-link.modify {
    margin-left: 10px;
  }
  &-text {
    font-size: clamp(16px, 2.3vw, 18px);
    line-height: clamp(23px, 3.4vw, 26px);
    font-weight: 400;
    color: #444;
    &.list {
      line-height: clamp(23px, 3.4vw, 32px);
    }
    &.modify {
      position: relative;
      padding: 30px 30px 35px 60px;
      margin-bottom: 5px;
      background-color: #289dcc;
      color: #fff;
      border-radius: 5px;
    }
    &.modify2 {
      font-weight: 600;
    }
    &.modify3 {
      font-weight: 600;
    }
  }
  .text-quote {
    font-size: 36px;
    color: #fff;
    position: absolute;
    top: 6px;
    left: 20px;
  }
}

///////// PAGE-TEAM /////////

.page-team {
  .container {
    padding-top: 30px;
    padding-bottom: 80px;
    background-color: #fafafa;
  }
  .btn-read {
    margin-top: 14px;
    cursor: pointer;
  }
  &-subbox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    border-radius: 5px;
    width: 100%;
    height: 250px;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url(../images/team/1.webp);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  &-title {
    margin-bottom: 12px;
    text-transform: uppercase;
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    line-height: 40px;
    letter-spacing: 0.1px;
  }
  &-subtext {
    line-height: 21px;
    max-width: 600px;
    color: #fff;
    letter-spacing: 0.1px;
    font-weight: 200;
    text-align: center;
    font-size: 15px;
  }
  &-content {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
  }
  &-block {
    display: flex;
    flex-direction: column;
    max-width: 360px;
  }
  &-box {
    &.pic {
      display: flex;
      position: relative;
      border-radius: 5px 5px 0 0;
      cursor: pointer;
      z-index: 1;
      &::after {
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: 5px 5px 0 0;
        background-color: rgba(0, 0, 0, 0.5);
        top: 0;
        left: 0;
        z-index: 100;
        content: "";
        opacity: 0;
        visibility: hidden;
        transition-duration: 500ms;
      }
      .social-btn {
        display: flex;
        position: absolute;
        justify-content: space-around;
        width: 80%;
        bottom: 25px;
        left: 10%;
        right: 10%;
        z-index: 300;
        transition-duration: 500ms;
        opacity: 0;
        visibility: hidden;
        &-link {
          display: inline-flex;
          color: #ffffff;
          font-size: 20px;
          padding: 6px;
          transition: color 0.1s ease;
          &:hover {
            color: #1bb2e9;
          }
        }
      }
      // &:hover::after {
      //   opacity: 1;
      //   visibility: visible;
      // }
      // &:hover .social-btn {
      //   opacity: 1;
      //   visibility: visible;
      // }
    }
    &.wrapper {
      padding: 15px 25px 25px 25px;
      box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
    }
  }
  .page-team-block:hover .page-team-box.pic::after {
    opacity: 1;
    visibility: visible;
  }
  .page-team-block:hover .social-btn {
    opacity: 1;
    visibility: visible;
  }
  &-img {
    display: inline-flex;
    border-radius: 5px 5px 0 0;
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
  }
  &-name {
    margin-bottom: 6px;
    color: #1a1a1a;
    line-height: 1.3;
    font-size: 17px;
    font-weight: 500;
  }
  &-caption {
    color: #ed3974;
    font-size: 15px;
    margin-bottom: 6px;
  }
  &-text-wrapper {
    &.active-clamp {
      overflow: hidden;
      display: -webkit-box;
      -ms-text-overflow: ellipsis;
      text-overflow: ellipsis;
      -ms-line-clamp: 8;
      -webkit-line-clamp: 9;
      display: -webkit-box;
      word-wrap: break-word;
      -webkit-box-orient: vertical;
    }
  }
  &-text {
    color: #6c6c6c;
    font-size: clamp(17px, 2.5vw, 16px);
    line-height: clamp(23px, 3.5vw, 25px);
    letter-spacing: 0.1px;
    line-height: 1.7;
    font-weight: 300;
    font-size: 15px;
    &:not(:last-child) {
      margin-bottom: 9px;
    }
  }
}

///////// FOOTER /////////

.footer {
  &-content {
    .container {
      display: flex;
      gap: 30px;
      padding-top: 50px;
      background-color: $footer-background;
    }
  }
  &-box {
    &:nth-of-type(1) {
      flex: 1 1 43%;
    }
    &:nth-of-type(2) {
      flex: 1 1 19%;
    }
    &:nth-of-type(3) {
      flex: 1 1 19%;
    }
    &:nth-of-type(4) {
      flex: 1 1 19%;
    }
    &-title {
      position: relative;
      white-space: nowrap;
      &:not(.modify) {
        margin-bottom: 20px;
      }
    }
    .box-caption {
      padding: 8px 12px;
      display: inline-flex;
      background-color: $footer-title-background;
      color: $secondary-text-color;
      font-size: 17px;
      font-weight: 400;
      line-height: 18px;

      &::after {
        content: "";
        height: 2px;
        background: #289dcc;
        display: inline-flex;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
      }
    }
    .logo {
      margin-top: 25px;
      &-name-first {
        color: rgba(255, 255, 255, 0.77);
      }
      &-description {
        color: rgba(255, 255, 255, 0.77);
      }
    }
    &-text {
      margin-top: 30px;
      margin-bottom: 100px;
      font-size: 14px;
      font-weight: 300;
      line-height: 21px;
      color: rgba(255, 255, 255, 0.77);
    }
    &-list {
      display: flex;
      flex-direction: column;
      gap: 7px;
    }
    &-item {
      border-bottom: 0.5px solid rgba(85, 85, 85, 0.7);
      padding-bottom: 7px;
    }
    &-link {
      font-size: 14px;
      font-weight: 300;
      line-height: 16px;
      letter-spacing: -0.2px;
      color: $secondary-text-color;
      transition: color 0.25s ease;
      &:hover {
        color: #289dcc;
      }
    }
  }
  &-copyright {
    .container {
      padding-bottom: 30px;
      display: flex;
      justify-content: space-between;
      background-color: $footer-background;
    }
    &-text {
      font-size: 14px;
      font-weight: 300;
      line-height: 17px;
      color: rgba(255, 255, 255, 0.63);
    }
  }
  .social-list {
    margin-right: 14px;
    display: flex;
    gap: 20px;
  }
  .social-item {
    display: inline-flex;
  }
  .social-link {
    font-size: 22px;
    & i {
      transition: color 0.1s ease;
    }
    .facebook {
      color: #3b5a9b;
    }
    .twitter {
      color: #1bb2e9;
    }
    .instagram {
      color: #3f729b;
    }
    .telegram {
      color: #25a3e1;
    }
    &:hover i {
      color: #db4545;
    }
  }
}
